<template>
  <el-form
    class="ling-ffc-compensation"
    label-position="right"
    label-width="100px"
    :model="compensation"
    v-loading="loading"
    ref="compensationForm">
    <p class="tips">如果播报的温度与实际的温度有偏差，可以通过调整该值来补偿偏差。例如播报30度，实际温度为32度，则可以填入2. 反之播报35度，实际温度为32度,则可以填入-3.</p>
    <el-form-item label="补偿参数：">
      <el-input type="number" v-model="compensation.ffc_compensation_parameter" placeholder="请输入FFC补偿参数"></el-input>
    </el-form-item>

    <el-form-item>
      <el-button type="primary" @click="handleSave('compensationForm')">保存</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
import getSettingInfo from '@/mixins/getSettingInfo'
import { mapActions } from 'vuex'
export default {
  name: 'LingFFCCompensation',
  mixins: [getSettingInfo],
  data () {
    return {
      loading: false,
      compensation: {
        ffc_compensation_parameter: ''
      }
    }
  },
  watch: {
    getSettingInfo: {
      handler (val) {
        this.compensation.ffc_compensation_parameter = +val.ffc_compensation_parameter
      },
      immediate: true,
      deep: true
    }
  },
  methods: {
    ...mapActions({
      setFFCCompensation: 'setFFCCompensation'
    }),
    handleSave (formName) {
      if (this.loading) {
        return
      }

      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          this.loading = true
          try {
            await this.setFFCCompensation(this.compensation)
          } catch (res) {}

          this.loading = false
        } else {
          console.log('error submit!!')
          return false
        }
      })
    }
  }
}
</script>

<style lang="scss">
.ling-ffc-compensation {

}
</style>



